body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(243, 243, 243);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  position: relative;
}

.page-content {
  position: relative;
  top: 0;
  height: 300px;
  z-index: 100;
  background-color: white;
}

/* alert部分 */
.alter-text {
  position: absolute;
  left: 2%;
  top: 21%;
  color: green;
  font-size: 1.5rem;
  font-weight: 500;
}

.hr-line {
  position: absolute;
  top: 45%;
  width: 100%;
}

/* svg部分 */
svg.user-icon {
  position: absolute;
  left: 3%;
  top: 57%;
  width: 5%;
  height: 5% ;
}

.users-icon {
  position: absolute;
  left: 3%;
  top: 73%;
  width: 4.5%;
  height: 4.5% ;
}

svg.time-icon {
  position: absolute;
  left: 2.3%;
  top: 89%;
  width: 6%;
  height: 6%;
}

/* entryTitle部分 */
.entry-title {
  font-size: 1.1rem;
}

.title-user-name {
  position: absolute;
  left: 10%;
  top: 56%;
}

.title-assign-type {
  position: absolute;
  left: 10%;
  top: 72%;
}

.title-assign-time {
  position: absolute;
  left: 10%;
  top: 88%;
}

/* EntryText部分 */

.text-user-name {
  position: absolute;
  right: 3%;
  top: 55%;
  font-size: 1.5rem;
  font-weight: 500;
}

.text-assign-type {
  position: absolute;
  right: 3%;
  top: 71%;
  font-size: 1.5rem;
  font-weight: 500;
  color: rgb(236, 144, 53);
}

.text-assign-time {
  position: absolute;
  right: 3%;
  top: 87.5%;
  font-size: 1.1rem;
  font-weight: 550;
  color: rgb(88, 192, 165);
  overflow: hidden;

}

.moving-text {
  white-space: nowrap;
  animation: wordsLoop 12s linear infinite normal;
}

.moving-text2 {
  white-space: nowrap;
  animation: wordsLoop2 12s linear infinite normal;
}

@keyframes wordsLoop {
  from {
    transform: translateX(0%);
  }
  to {
      transform: translateX(-105%);
  }
}

@keyframes wordsLoop2 {
  from {
    /* transform: translateX(105%);
    transform: translateY(-100%); */
    left: 100%
  }
  to {
    left: -100%;
    /* transform: translateX(0%);
    transform: translateY(0%); */
  }
}
